            <h3>Ï∞∏Í∞Ä Ïã†Ï≤≠ÏùÑ ÌïòÏãúÍ≤†ÏäµÎãàÍπå? </h3>
        </div>
        <hr>
        <div class="entry-insert-content">
            <p>ÎèôÌñâ Ïã†Ï≤≠ÏùÑ ÏúÑÌïú Ï†ïÎ≥¥Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî</p>
        </div>
        <div class="entry-insert-car">
            <div class="col">
                <p><span>Ï∞®ÎüâÏú†Î¨¥</span></p>
                <label><input type="radio" v-model="recruEntry.entryCar" value="1">ÏûàÏùå</label>
                <label><input type="radio" v-model="recruEntry.entryCar" value="0" checked>ÏóÜÏùå</label>
            </div>
