    methods : {
        addGear : function(menu){     
            //Ïû•ÎπÑ ÏûÖÎ†•Ïãú Ï∂îÍ∞Ä Í∞ÄÎä•           
            const box = document.getElementById(menu);
            const li = document.createElement('li');
            menu = menu.substring(0,menu.indexOf("-b"));

            let str = "<input type='text' class='"+menu+"-name' style='padding:5px;margin:2px;border:1px solid lightgray;border-radius:4px' placeholder='Ïû•ÎπÑÏù¥Î¶Ñ' size='18' maxlength='18'>"
                        +" <select class='"+menu+"-type' style='padding:5px;margin:2px;border:1px solid lightgray;border-radius:4px;'>"
                            +"<option value='' selected disabled>Ïû•ÎπÑ Î∂ÑÎ•ò</option>"
                            +"<option value='ÌÖêÌä∏'>ÌÖêÌä∏</option>"
                            +"<option value='ÌÉÄÌîÑ'>ÌÉÄÌîÑ</option>"
                            +"<option value='Í∞ÄÍµ¨'>Í∞ÄÍµ¨</option>"
                            +"<option value='Ïπ®Íµ¨'>Ïπ®Íµ¨</option>"
                            +"<option value='Ï°∞Î¶¨ÎèÑÍµ¨'>Ï°∞Î¶¨ÎèÑÍµ¨</option>"
                            +"<option value='Ï°∞Î™Ö'>Ï°∞Î™Ö</option>"
                            +"<option value='ÏàòÎÇ©'>ÏàòÎÇ©</option>"
                            +"<option value='Í≥µÍµ¨'>Í≥µÍµ¨</option>"
                            +"<option value='ÎÉâÎÇúÎ∞©'>ÎÉâÎÇúÎ∞©</option>"
                            +"<option value='Í∏∞ÌÉÄ'>Í∏∞ÌÉÄ</option>"
                        +"</select>"
                        +"<input type='number' class='"+menu+"-num gear-num' style='width:50px;padding:5px;margin:2px;border:1px solid lightgray;border-radius:4px;' value='1' min='1' max='999'>Í∞ú"
                        +"<button type='button' class='btn' style='width:17px; height:17px;border-radius:50%;background:crimson;border:none;color:white;margin-left:2px' >x</button>";
            li.innerHTML = str;
