        entryInsert : function(){
            //Ïû•ÎπÑÎ™©Î°ùÏùÑ Ïä§Ìä∏ÎßÅ ÌÉÄÏûÖÏúºÎ°ú Î¨∂Í∏∞
            let gearNames = document.querySelectorAll('.entry-gear-name');
            let gearTypes = document.querySelectorAll('.entry-gear-type');
            let gearNum = document.querySelectorAll('.entry-gear-num');
            for(let i=0 ; i<gearNames.length ; i++){
                //ÌäπÏàòÎ¨∏Ïûê Ï≤¥ÌÅ¨ Ï†ïÍ∑úÏãù
                const regExp = /[\{\}\[\]\/?.,;:|\)*~`!^\-_+<>@\#$%&\\\=\(\'\"]/g
                if(regExp.test(gearNames[i].value)){
                    Swal.fire('Ïû•ÎπÑ Ïù¥Î¶ÑÏùÑ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî',"< > @ . , ; : & * ^ / $ Îì±Ïùò ÌäπÏàòÎ¨∏ÏûêÎäî ÏûÖÎ†•Ìï† Ïàò ÏóÜÏäµÎãàÎã§.",'warning');
                    return;
                }
                if(gearNames[i].value===''){
                    Swal.fire('Ïû•ÎπÑ Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî','Ï∂îÍ∞ÄÌï† Ïû•ÎπÑÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞ x Î≤ÑÌäºÏùÑ ÎàåÎü¨ ÏßÄÏõåÏ£ºÏÑ∏Ïöî','warning');
                    gearNames[i].focus();
                    return;
                }
            }
            let gearList = '';
            for(let i=0 ; i<gearNames.length ; i++){
                if(i !=0) gearList+= ',';
                gearList += gearNames[i].value+','+gearTypes[i].value+','+gearNum[i].value ;
            }
            this.recruEntry.entryGear = gearList;
            const component = this;
            //Ïã†Ï≤≠ ÌÖåÏù¥Î∏îÏóê Ïù∏ÏÑúÌä∏ÌïòÍ∏∞ 
            this.recruEntry.recruId = this.recruId;
            let recruEntry = this.recruEntry;
            fetch('http://localhost:8087/java/recru/entry',{
                method : "POST",
                headers : {"Content-Type" : "application/json"},
                body : JSON.stringify(recruEntry )
            }) 
            .then(Response => Response.json())  
            .then(data => { 
                if(data==1){
                    Swal.fire('Ïã†Ï≤≠ ÏôÑÎ£åÌñàÏäµÎãàÎã§.','Î™®ÏßëÏûêÍ∞Ä Ïã†Ï≤≠ÏùÑ ÏàòÎùΩÌï† Í≤ΩÏö∞ ÏïåÎ¶ºÏù¥ Í∞ëÎãàÎã§','success')
                    .then(result=>{
                        component.$emit('close-recru')
                        this.$router.go(0)
                    })
                }else{
                    Swal.fire('Î¨∏Ï†ú Î∞úÏÉù!','Ïã†Ï≤≠Ïù¥ Ï≤òÎ¶¨ÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§. Î¨∏Ï†úÍ∞Ä Í≥ÑÏÜçÎê† Í≤ΩÏö∞ Î¨∏ÏùòÌï¥Ï£ºÏÑ∏Ïöî','error');
                }
                //Detail ÌéòÏù¥ÏßÄÏùò ÏÉÅÌÉú Î≥ÄÍ≤Ω
