<template>
  <div class="entry-card-box ">
      <div class="entry-card-row row">
          <div class="entry-card-img col">
            <img :src="'http://localhost:8087/java/profile/'+storedProfile.imagePath+'/'+storedProfile.storedName">
          </div>
          <div class="entry-card-info col">
              <div class="entry-card-contents">
                  <h3>{{user.nickname}}</h3>
                  <p>{{user.sex}}, {{userage}}, Ï∞® {{entryCard.entryCar==1?'ÏûàÏùå':'ÏóÜÏùå'}}</p>
                  <p><span>Î≥¥Ïú†Ïû•ÎπÑ : </span> {{gearList}}</p>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
    export default{
        props:{
            entryCard : Object
        },
        data: function(){
            return {
                user :{},
                storedProfile : ''
            }
        },
        created(){
            var email = this.entryCard.memberId;
            fetch('http://localhost:8087/java/member/' +email)
                .then(result => result.json())
                .then(result => {
                    this.user = result;
                }).catch(err => console.log(err));

                fetch('http://localhost:8087/java/profile/' + email)
                .then(result => result.json())
                .then(result => {
                    this.storedProfile = result;
                }).catch(err => console.log(err));
                //Îß¥Î≤Ñ Ï†ïÎ≥¥ Î∞õÏïÑÏÑú ÏÑ±Î≥ÑÍ≥º, ÏÉùÏùºÏóêÏÑú Ïó∞Î†πÎåÄ Ï∂îÏ∂ú
        },
        computed :{
            gearList(){
                //ÌïÑÏöîÌïú Ïû•ÎπÑ Î∞∞Ïó¥ Ï†ïÎ¶¨
                let gearList = this.entryCard.entryGear.split(',');
                let str = gearList[0]+'('+gearList[1]+') '+gearList[2]+'Í∞ú'
                for(let i=3; i<gearList.length; i+=3){
                    str = str+', '+gearList[i]+'('+gearList[i+1]+') '+gearList[i+2]+'Í∞ú';
                }
                return str;
            },
            userage(){
                //Ïó∞Î†πÎåÄ Í≥ÑÏÇ∞
                const today = new Date();          
                let birth = this.user.birth;
                birth = typeof birth === 'string' ? birth.substring(0, 4) : '';
                let age = today.getFullYear() - birth + 1;
                
                if(age<30){
                    return '20ÎåÄ'
                }else if(age<40){
                    return '30ÎåÄ'
                }else if(age<50){
                    return '40ÎåÄ'
                }else{
                    return '50ÎåÄ Ïù¥ÏÉÅ'
                }
            },
        },
    }
    
</script>
