<template>
    <div>
      <div id="map" class="map"></div>
      <button onclick="panTo()">ÎèÑÏ∞©ÏßÄ</button> 
    </div>
  </template>
  
  <script>
    export default {
      mounted() {
        window.kakao && window.kakao.maps ?  this.initMap() : this.addKakaoMapScript();
      },
      props :{startingPoint: String,
              campingPoint : String},
      data() {
        return {
          geocoder:null,
        }
      },
      methods: {
        addKakaoMapScript() {
          const script = document.createElement("script");
          /* global kakao */
          script.onload = () => kakao.maps.load(this.initMap);
          script.src =
            "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=3f57d02e134f1067307cbaec0b42c437&libraries=services";
          document.head.appendChild(script);
        },
        initMap() {
          var container = document.getElementById("map"); //ÏßÄÎèÑÎ•º Îã¥ÏùÑ ÏòÅÏó≠Ïùò DOM Î†àÌçºÎü∞Ïä§
          var options = {
            //ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï† Îïå ÌïÑÏöîÌïú Í∏∞Î≥∏ ÏòµÏÖò
            center: new kakao.maps.LatLng(33.450701, 126.570667), //ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
            level: 7 //ÏßÄÎèÑÏùò Î†àÎ≤®(ÌôïÎåÄ, Ï∂ïÏÜå Ï†ïÎèÑ)
          };
  
          var map = new kakao.maps.Map(container, options); //ÏßÄÎèÑ ÏÉùÏÑ± Î∞è Í∞ùÏ≤¥ Î¶¨ÌÑ¥
  
          var positions=[
            {
              title: this.startingPoint, 
              latlng: null
            },
            {
              title: this.campingPoint, 
              latlng: null
            }
          ]
          // Ï£ºÏÜå-Ï¢åÌëú Î≥ÄÌôò Í∞ùÏ≤¥Î•º ÏÉùÏÑ±
          this.geocoder = new kakao.maps.services.Geocoder();
          this.geocoder.addressSearch(this.startingPoint, function (result, status) {
  
            // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Í≤ÄÏÉâÏù¥ ÏôÑÎ£åÎêêÏúºÎ©¥ 
            if (status === kakao.maps.services.Status.OK) {
              var coords = new kakao.maps.LatLng(result[0].y, result[0].x);        
              positions[0].latlng = coords;   
              // Í≤∞Í≥ºÍ∞íÏúºÎ°ú Î∞õÏùÄ ÏúÑÏπòÎ•º ÎßàÏª§Î°ú ÌëúÏãú
              var marker = new kakao.maps.Marker({
                map: map,
                position: coords
              });

              // Ïù∏Ìè¨ÏúàÎèÑÏö∞Î°ú Ïû•ÏÜåÏóê ÎåÄÌïú ÏÑ§Î™Ö
              var infowindow = new kakao.maps.InfoWindow({
                content: `<div style="width:120px;text-align:center;padding:3px 0; text-align:center">${positions[0].title}</div>`
              });
              infowindow.open(map, marker);
            }
          });
  
          this.geocoder.addressSearch(this.campingPoint, function (result, status) {
            if (status === kakao.maps.services.Status.OK) {
              var coords = new kakao.maps.LatLng(result[0].y, result[0].x);
              positions[1].latlng = coords;  
              var marker = new kakao.maps.Marker({
                map: map,
                position: coords
              });
  
              // Ïù∏Ìè¨ÏúàÎèÑÏö∞Î°ú Ïû•ÏÜåÏóê ÎåÄÌïú ÏÑ§Î™ÖÏùÑ ÌëúÏãúÌï©ÎãàÎã§
              var infowindow = new kakao.maps.InfoWindow({
                content: `<div style="width:120px;text-align:center;padding:3px 0; text-align:center">${positions[1].title}</div>`
              });
              infowindow.open(map, marker);
  
              // ÏßÄÎèÑÏùò Ï§ëÏã¨ÏùÑ Í≤∞Í≥ºÍ∞íÏúºÎ°ú Î∞õÏùÄ ÏúÑÏπòÎ°ú Ïù¥Îèô
              
              const bounds = positions.reduce(
                  (bounds, position) => bounds.extend(position.latlng),
                  new kakao.maps.LatLngBounds()
              );
             map.setBounds(bounds);
              }
          });
        },
        panTo :function() {
            // Ïù¥ÎèôÌï† ÏúÑÎèÑ Í≤ΩÎèÑ ÏúÑÏπòÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§ 
            var moveLatLon = new kakao.maps.LatLng(33.450580, 126.574942);
            map.panTo(moveLatLon);            
        }        
      }
    }
    
  </script>
  
  <style scoped>
    .map {
      width: 100%;
      height: 400px;
    }
  </style>