<template>
  <div class="entry-card-box ">
      <div class="entry-card-row row">
          <div class="entry-card-img col">
            <img :src="'http://localhost:8087/java/profile/'+storedProfile.imagePath+'/'+storedProfile.storedName">
          </div>
          <div class="entry-card-info col">
              <div class="entry-card-contents">
                  <h3>{{user.nickname}}</h3>
                  <p class="entry-date">{{entryCard.entryDate}}</p>
                  <p>{{user.sex}}, {{userage}}, Ï∞® {{entryCard.entryCar==1?'ÏûàÏùå' :'ÏóÜÏùå'}}</p>
                  <p><span>Î≥¥Ïú†Ïû•ÎπÑ : </span> {{gearList}}</p>
              </div>
              <div class="entry-yesno-btn row">
                  <button type="button" class="accept-btn" @click="entryAccept">ÏàòÎùΩ</button>
                  <button type="button" class="refuse-btn" @click="entryRefusal">Í±∞Ï†à</button>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
    import Swal from 'sweetalert2';
    export default{
        props:{
            entryCard : Object
        },
        data: function(){
            return {
                user :{},
                storedProfile : '',
                updateStatus : {
                    entryStatus : 0,    // Ïã†Ï≤≠ÏÉÅÌÉú : 0Ïã†Ï≤≠Ï§ë, 1ÏàòÎùΩ, 2Í±∞Ï†à, 3Ï∑®ÏÜåÎåÄÍ∏∞, 4Ï∑®ÏÜå
                    entryId : 0
                }
            }
        },
        created(){
            this.entryCard.entryDate = this.entryCard.entryDate.toLocaleString('en-US');
            var email = this.entryCard.memberId;
            fetch('http://localhost:8087/java/member/' +email)
                .then(result => result.json())
                .then(result => {
                    this.user = result;
                }).catch(err => console.log(err));

                fetch('http://localhost:8087/java/profile/' + email)
                .then(result => result.json())
                .then(result => {
                    this.storedProfile = result;
                }).catch(err => console.log(err));
        },
        computed : {
            gearList(){
                //ÌïÑÏöîÌïú Ïû•ÎπÑ Î∞∞Ïó¥ Ï†ïÎ¶¨
                let gearList = this.entryCard.entryGear.split(',');
                let str = gearList[0]+'('+gearList[1]+') '+gearList[2]+'Í∞ú'
                for(let i=3; i<gearList.length; i+=3){
                    str = str+', '+gearList[i]+'('+gearList[i+1]+') '+gearList[i+2]+'Í∞ú';
                }
                return str;
            },
            userage(){
                //Ïó∞Î†πÎåÄ Í≥ÑÏÇ∞
                const today = new Date();          
                let birth = this.user.birth;
                birth = typeof birth === 'string' ? birth.substring(0, 4) : '';
                let age = today.getFullYear() - birth + 1;
                
                if(age<30){
                    return '20ÎåÄ'
                }else if(age<40){
                    return '30ÎåÄ'
                }else if(age<50){
                    return '40ÎåÄ'
                }else{
                    return '50ÎåÄ Ïù¥ÏÉÅ'
                }
            },
        },
        methods : {
            entryAccept (){
                //Ïã†Ï≤≠ ÏàòÎùΩ
                Swal.fire({
                    icon: 'warning',
                    title: 'Ïã†Ï≤≠ÏùÑ ÏäπÏù∏ÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
                    text: 'ÎèôÌñâÏùÑ ÏàòÎùΩÌï©ÎãàÎã§',
                    position: 'center-center',
                    showCancelButton: true,              
                }).then(result => {
                    const component = this;
                    if(result.isConfirmed){
                        this.updateStatus.entryStatus = 1;
                        this.updateStatus.entryId = this.entryCard.entryId;
                        let updateInfo = this.updateStatus;
                        //Îì±Î°ùÏÉÅÌÉúÎ≥ÄÌôî
                        fetch("http://localhost:8087/java/recru/entry",{
                            method : "PUT",
                            headers : {"Content-Type" : "application/json"},
                            body : JSON.stringify(updateInfo)
                        })
                        .then(Response => Response.json())  
                        .then(data => { 
                            console.log(data)
                            Swal.fire('ÎèôÌñâÏùÑ ÏàòÎùΩÌïòÏòÄÏäµÎãàÎã§.','Ìï®Íªò Ïó¨ÌñâÏùÑ Îñ†ÎÇòÏöî!','success');
                        }).catch(err=>console.log(err))
                        component.$router.go(0) ;
                    }
                })
            },
            entryRefusal(){
                Swal.fire({
                    icon: 'warning',
                    title: 'Ïã†Ï≤≠ÏùÑ Í±∞Ï†àÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
                    text: 'Ìï®Íªò Ìï† Ïàò ÏóÜÏäµÎãàÎã§.',
                    position: 'center-center',
                    showCancelButton: true,              
                }).then(result => {
                    if(result.isConfirmed){
                        this.updateStatus.entryStatus = 2;
                        this.updateStatus.entryId = this.entryCard.entryId;
                        let updateInfo = this.updateStatus;
                        let component = this;
                         //Îì±Î°ùÏÉÅÌÉúÎ≥ÄÌôî
                         fetch("http://localhost:8087/java/recru/entry",{
                            method : "PUT",
                            headers : {"Content-Type" : "application/json"},
                            body : JSON.stringify(updateInfo)
                        })
                        .then(Response => Response.json())  
                        .then(data => { 
                            console.log(data)
                            Swal.fire('ÎèôÌñâÏùÑ Í±∞Ï†àÌïòÏòÄÏäµÎãàÎã§.','Îã§ÏùåÏóê Ìï®ÍªòÌï¥Ïöî','');
                        }).catch(err=>console.log(err))
                        component.$router.go(0) ;
                    }
                })
                //Îì±Î°ùÏÉÅÌÉúÎ≥ÄÌôî
            },

        }
    }
    
</script>
<style scoped src="@/assets/css/recruit/entryCard.css" />
