                        <p><span>ÏÑ±Î≥Ñ </span>{{recruPost.sex}}</p>
                        <p><span>Ïó∞Î†πÎåÄ  </span>{{userage}}</p>
                        <p><span>Ï∞®Îüâ Ïú†Î¨¥ </span>{{recruPost.carYn==1 ? 'ÏûàÏùå':'ÏóÜÏùå'}}</p>
                        <br>
                        <h3 style="font-weight: bold;">Ïù¥Îü∞ Î∂Ñ</h3>
                        <p><span>ÏÑ±Î≥Ñ  </span>{{userSex}}</p>
                        <p><span>Ïó∞Î†πÎåÄ  </span>{{recruPost.wishAge}}</p>
                        <br>
                        <h3 style="font-weight: bold;">Ìï®Íªò Ìï¥Ïöî</h3>
                        <p><span>Ïó¨Ìñâ ÏòàÏ†ï ÎÇ†Ïßú  </span>{{recruPost.goDate}} ~ {{recruPost.comeDate}}</p>
                        <p><span>Ï∂úÎ∞úÏßÄÏó≠  </span>{{recruPost.startingPoint}}</p>
                        <p><span>ÎèÑÏ∞©ÏßÄ </span>{{recruPost.campingPoint}}</p>
                        <p><span>Î™®Ïßë Ïù∏Ïõê  </span>{{recruPost.recruNum}}</p>
