                    <button v-if="userRole==0 && rStatus==0" class="btn-green hover-shadow" type="button" @click="entryInsertForm">ÎèôÌñâÏã†Ï≤≠</button>
                    <!-- Ïã†Ï≤≠ÏûêÏù∏ Í≤ΩÏö∞ -->
                    <button v-if="userRole==2" type="button" style="color: green;background: rgba(228,239,231,0.7);font-weight: bold;">Ïã†Ï≤≠ Ï§ë</button>
                    <button v-if="userRole==2||userRole==3" class="hover-shadow" type="button" @click="entryDelete" style="color:gray; background: nlightgray; ">Ïã†Ï≤≠ Ï∑®ÏÜå</button>
                </div>
                <div v-if="userRole==1" class="recru-writer-btn">
                    <!-- Î™®ÏßëÏûê(ÏûëÏÑ±Ïûê)Ïù∏ Í≤ΩÏö∞ -->
                    <button v-if="rStatus==0" type="button" @click="recruFinish">Î™®ÏßëÏôÑÎ£å</button>
                    <button v-if="rStatus==0" type="button" @click="recruUpdate">ÏàòÏ†ï</button>
                    <button type="button" @click="userDelete">ÏÇ≠Ï†ú</button>
                    <button v-if="rStatus==1" type="button" @click="recruReview">ÌõÑÍ∏∞Îì±Î°ù</button>
                </div>
                <div v-if="userRole==4" class="recru-writer-btn">
