                    </div>
                </div>
            </div>
            <!-- Ïã†Ï≤≠ ÏàòÎùΩÎêú ÎÇ¥Ïó≠ :  -->
            <div class="recru-detail-sol recru-entry-post">
                <h3>ÌòÑÏû¨ ÎèôÌñâÏûê ({{entryCount}}/{{recruPost.recruNum}})</h3>
                <div class="recru-entry-list">
