import EntryCard from '@/components/recruit/EntryCard.vue';
import RecruDetailImage from '@/components/recruit/RecruDetailImage.vue';
import RecruMap from '@/components/recruit/RecruMap.vue';
import DepositStatus from '@/components/recruit/DepositStatus.vue';
import EntryInsert from '@/components/recruit/EntryInsert.vue';
import ModalView from '@/components/recruit/ModalView.vue';
import Swal from 'sweetalert2';
import RecruStatus from './RecruStatus.vue';
export default{
    name : "RecruDetail",
    props : {
        recruId : String
    },
    components :{
    EntryStandByCard,
    EntryCard,
    RecruDetailImage,
    RecruMap,
    DepositStatus,
    EntryInsert,
    ModalView,
    RecruStatus,
},
    data:function(){
        return{
            //Î°§ ÏßÄÏ†ï : 0ÏùºÎ∞òÏú†Ï†Ä, 1Î™®ÏßëÏûê, 2Ïã†Ï≤≠Ï§ëÏù∏ ÏÇ¨Îûå, 3Ïã†Ï≤≠ÏàòÎùΩÎêú ÏÇ¨Îûå, 4Í¥ÄÎ¶¨Ïûê 
            memberId : sessionStorage.getItem("email"),
            storedProfile : '',
            userRole : 0,
            recruPost : {},
            entryPost : [],
            entryingMember : '',
            entredMember : '',
            statusClass:'',
            notePost : {},
            sendRecruInfo : {
                recruId : 0, 
                recruStatus : 1,
                memberId : ''
            },
            isModalViewed : false,
        }
    },
    created (){
        this.loadRecruData();
    },
    computed : {
        userage(){
            //Ìù¨Îßù Ïó∞Î†πÎåÄ Í≥ÑÏÇ∞
            const today = new Date();          
            let birth = this.recruPost.birth;
            birth = typeof birth === 'string' ? birth.substring(0, 4) : '';
            let age = today.getFullYear() - birth + 1;
            
            if(age<30){
                return '20ÎåÄ'
            }else if(age<40){
                return '30ÎåÄ'
            }else if(age<50){
                return '40ÎåÄ'
            }else{
                return '50ÎåÄ Ïù¥ÏÉÅ'
            }
        },
        entryCount(){
            //ÌòÑÏû¨ ÏàòÎùΩÎêú ÎèôÌñâÏûê Ïàò Í≥ÑÏÇ∞
            let entryCount = 0
            this.entryPost.forEach(entry=>{
                if(entry.entryStatus==1){
                    entryCount ++;
                }
            })
            return entryCount;
        },
        userSex(){
            //Ìù¨Îßù ÏÑ±Î≥Ñ
            let gender = this.recruPost.wishSex;
            if(gender==1){
                return 'ÎÇ®Ïûê'
            }else if(gender==2){
                return 'Ïó¨Ïûê'
            }else{
                return 'Î¨¥Í¥Ä'
            }
        },
        rStatus(){
            let status = this.recruPost.recruStatus;
            return status;
        }
    },
    methods :{
        loadRecruData : function(){
            // ÏÑúÎ≤ÑÏóêÏÑú Îã®Í±¥Ï°∞Ìöå
            let recruId = this.recruId;
            let component = this;
            fetch("http://localhost:8087/java/recru/"+recruId)
            .then((response) =>response.json()) 
            .then(data => { 
                component.recruPost = data;  
                console.log(component.recruPost);
        
                // ÏÑúÎ≤ÑÏóêÏÑú Î™®ÏßëÍ∏ÄÏóê ÎåÄÌïú Ï∞∏Í∞ÄÎ™©Î°ù Ï°∞Ìöå
                let recruId = 0;
                recruId = this.recruId;
                fetch("http://localhost:8087/java/recru/entry/"+recruId)
                .then((response) =>response.json()) 
                .then(data => { 
                    component.entryPost = data;  
                    //Ï∞∏Í∞ÄÎ™©Î°ù Ï†ÄÏû•         
                    //Î°§ ÏßÄÏ†ï : 0ÏùºÎ∞òÏú†Ï†Ä, 1Î™®ÏßëÏûê, 2Ïã†Ï≤≠Ï§ëÏù∏ ÏÇ¨Îûå, 3Ïã†Ï≤≠ÏàòÎùΩÎêú ÏÇ¨Îûå, 4Í¥ÄÎ¶¨Ïûê 
                    component.entryPost.forEach(entry => {
                        if(entry.memberId == component.memberId && entry.entryStatus ==0){
                            //Ïã†Ï≤≠Ï§ëÏù∏ ÏÇ¨Îûå
                            component.entryingMember+=entry.memberId+' ';
                            component.userRole = 2;
                        }else if(entry.memberId == component.memberId && entry.entryStatus ==1){
                            //Ïã†Ï≤≠ÏàòÎùΩÎêú ÏÇ¨Îûå
                            component.entredMember+=entry.memberId+' ';
                            component.userRole = 3;
                        }
                    })
                    let writer = component.recruPost.memberId;
                    if(component.userRole==2 || component.userRole==3){
                    }else if(component.memberId == writer){
                        component.userRole = 1;
                    }else if(this.$store.state.auth == 0){
                        component.userRole = 4;
                    }else{
                        component.userRole = 0;
                    }
                    console.log('role ' + component.userRole)
                }).catch(err=>console.log(err));
            })
            fetch('http://localhost:8087/java/profile/' + this.$store.state.email)
                .then(result => result.json())
                .then(result => {
                    this.storedProfile = result;
            }).catch(err => console.log(err));
            
        }, 
        yyyyMMddhhmmss : function(value){
            if(value == '') return '';
    
            var js_date = new Date(value);

            // Ïó∞ÎèÑ, Ïõî, Ïùº, Ïãú, Î∂Ñ, Ï¥à Ï∂îÏ∂ú
            var year = js_date.getFullYear();
            var month = js_date.getMonth() + 1;
            var day = js_date.getDate();
            var hours = ('0' + js_date.getHours()).slice(-2); 
            var minutes = ('0' + js_date.getMinutes()).slice(-2);
            var seconds = ('0' + js_date.getSeconds()).slice(-2); 
            
            if(month < 10) {month = '0' + month;}
            if(day < 10){day = '0' + day;}
        
            return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes  + ':' + seconds;
        },
        recruFinish : function(){
            let component = this;
            //Î™®Ïßë ÏôÑÎ£å Î≤ÑÌäº
            Swal.fire({
                title: 'Î™®ÏßëÏùÑ ÏôÑÎ£åÌïòÏãúÍ≤†ÏäµÎãàÍ∞Ä?',
                text: 'ÎåÄÍ∏∞Ï§ëÏù∏ Ïã†Ï≤≠ÏùÑ Î™®Îëê ÌôïÏù∏ÌïòÏÖ®ÎÇòÏöî? Î™®ÏßëÏôÑÎ£åÎäî ÎêòÎèåÎ¶¥ Ïàò ÏóÜÏäµÎãàÎã§.',
                icon: 'warning',
                
                showCancelButton: true, // cancelÎ≤ÑÌäº Î≥¥Ïù¥Í∏∞. Í∏∞Î≥∏ÏùÄ ÏõêÎûò ÏóÜÏùå
                confirmButtonText: 'Î™®ÏßëÏôÑÎ£å', // confirm Î≤ÑÌäº ÌÖçÏä§Ìä∏ ÏßÄÏ†ï
                cancelButtonText: 'Ï∑®ÏÜå', // cancel Î≤ÑÌäº ÌÖçÏä§Ìä∏ ÏßÄÏ†ï
                })
                .then(result => {
                    //Î™®ÏßëÏÉÅÌÉúÎ•º ÏôÑÎ£åÎ°ú Î≥ÄÍ≤Ω, Î≥¥Ï¶ùÏ†ïÎ≥¥ insert
                    if (result.isConfirmed) { 
                        component.sendRecruInfo.recruId = component.recruId;
                        component.sendRecruInfo.memberId = component.memberId;
                        component.sendRecruInfo.recruStatus = 1;
                        fetch('http://localhost:8087/java/recru',{
                            method : "PUT",
                            headers : {"Content-Type" : "application/json"},
                            body :  JSON.stringify(component.sendRecruInfo)
                        }) 
                        .then(Response => Response.json())  
                        .then(data => { 
                            Swal.fire('ÏäπÏù∏Ïù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.', 'Ï¶êÍ±∞Ïö¥ Ïó¨Ìñâ ÎêòÏÑ∏Ïöî!', 'success');
                            component.recruPost.recruStatus=1;
                            component.loadRecruData();
                        }).catch(err=>console.log(err))
                    }
                });
            
        },
        reportItem() {
            console.log(this.$store.state.email);
            if(this.$store.state.email==null){
                console.log('ÎÑêÏûÖÎãàÎã§')
                Swal.fire({
                        title: 'Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.',
                        text: "Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌïòÍ≤†ÏäµÎãàÍπå?",
                        icon: 'warning',
                        showCancelButton: true,
                        // confirmButtonColor: '#3085d6',
                        // cancelButtonColor: '#d33',
                        confirmButtonText: 'ÎÑ§',
                        cancelButtonText : 'ÏïÑÎãàÏò§'
                    }).then((result) => {
                    if (result.isConfirmed) {
                        this.$router.push({ name: 'LoginSignup'})
                    }
                })
            }else{
                let item = Swal.fire({
                    title: 'Ïã†Í≥†',
                    html:
                        '<select id="swal-input1" class="swal2-select" style="font-size:13px;">' +
                        '<option value="" disabled="">Ïã†Í≥† Î∂ÑÎ•ò</option>' +
                        '<option value="ÏûòÎ™ªÎêú Ï†ïÎ≥¥">ÏûòÎ™ªÎêú Ï†ïÎ≥¥</option>' +
                        '<option value="Í≤åÏãúÍ∏Ä Í∑úÏ†ï ÏúÑÎ∞ò">Í≤åÏãúÍ∏Ä Í∑úÏ†ï ÏúÑÎ∞ò</option>' +
                        '<option value="Í∏∞ÌÉÄ">Í∏∞ÌÉÄ</option>' +
                        '</select>' +
                        '<textarea id="swal-input2" class="swal2-textarea" style="resize:none; width:80%; font-size:12px;" maxlength="450" placeholder="Ïã†Í≥† ÏÇ¨Ïú†Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"></textarea>',
                    focusConfirm: false,
                    showCancelButton: true,
                    confirmButtonText: 'Ïã†Í≥†',
                    cancelButtonText: 'Ï∑®ÏÜå',
                    confirmButtonColor: 'rgba(6,68,32,0.8)',
                    preConfirm: () => {
                        let fetchData = {
                            "boardId": this.recruId,
                            "boardDivision": 1,
                            "reportDivision": document.getElementById('swal-input1').value,
                            "reportContent": document.getElementById('swal-input2').value,
                            "email": this.$store.state.email
                        }

                        console.log(fetchData);
                        fetch('http://localhost:8087/java/report', {
                            method: 'POST',
                            headers: {'Content-Type': 'application/json' },
                            body: JSON.stringify(fetchData)
                        }).then(result => result.text())
                            .then(result => {
                                if (result == "true") {
                                    Swal.fire({
                                        icon: 'success',
                                        title: 'Ïã†Í≥† ÏôÑÎ£å !',
                                        toast: true,
                                        showConfirmButton: false,
                                        timer: 1500,
                                        timerProgressBar: true,
                                        didOpen: (toast) => {
                                            toast.addEventListener('mouseenter', Swal.stopTimer)
                                            toast.addEventListener('mouseleave', Swal.resumeTimer)
                                            //this.$router.push({ path: '/recru/detail/' + this.recruId, });
                                        }
                                    })
                                } else {
                                    Swal.fire({
                                        icon: 'error',
                                        title: 'Ïã†Í≥† Ïã§Ìå® !',
                                        text:'Í≥ÑÏÜç Ïã§Ìå®ÌïòÎ©¥ Í≥†Í∞ùÏÑºÌÑ∞Ïóê Î¨∏ÏùòÌï¥Ï£ºÏÑ∏Ïöî.',
                                        toast: true,
                                        showConfirmButton: false,
                                        timer: 1500,
                                        timerProgressBar: true,
                                        didOpen: (toast) => {
                                            toast.addEventListener('mouseenter', Swal.stopTimer)
                                            toast.addEventListener('mouseleave', Swal.resumeTimer)
                                        }
                                    })
                                }
                                console.log(result);
                            })
                        return false;
                    }
                })
            }
        },
        gearList(gears){
            //ÌïÑÏöîÌïú Ïû•ÎπÑ Î∞∞Ïó¥ Ï†ïÎ¶¨
            if(gears){
                let gearList = gears.split(',');
                let str = '';
                for(let i=0; i<gearList.length; i+=3){
                    if(i!=0) str+= ', ';
                    str += gearList[i]+'('+gearList[i+1]+') '+gearList[i+2]+'Í∞ú';
                }
                return str;
            }
        },
        entryInsertForm : function(){
            if(!this.memberId){
                Swal.fire({
                        title: 'Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.',
                        text: "Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌïòÍ≤†ÏäµÎãàÍπå?",
                        icon: 'warning',
                        showCancelButton: true,
                        // confirmButtonColor: '#3085d6',
                        // cancelButtonColor: '#d33',
                        confirmButtonText: 'ÎÑ§',
                        cancelButtonText : 'ÏïÑÎãàÏò§'
                    }).then((result) => {
                    if (result.isConfirmed) {
                        this.$router.push({ name: 'LoginSignup'})
                    }
                })
            }else{
                this.isModalViewed = true;
            }
        },
        recruUpdate : function(){
            this.$router.push({name : 'RecruUpdate',params : {recruId : this.recruId}})
        },
        userDelete : function(){
            if(this.entryCount >0){
               alert('ÏÇ≠Ï†úÎ∂àÍ∞Ä')
            }else{
                this.changeShowInfo(1)
            }
        },
        adminDelete : function(){
            this.changeShowInfo(2)
        },
        recruReview : function(){
            this.$router.push({name : 'RecruReview',params : {recruId : this.recruId}})
        },
        changeShowInfo : function(status){
            var changeInfo = {
                recruId : this.recruId,
                status : status
            };
            console.log(changeInfo)
            fetch('http://localhost:8087/java/recru/showStatus', {
                method : "PUT",
                headers : {"Content-Type" : "application/json"},
                body :  JSON.stringify(changeInfo)
            }).then(result => result.text())
                    .then(result => {
                        console.log(result)
                        if (result == 1) {
                            Swal.fire({
                                icon: 'success',
                                title: 'ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§',
                                toast: true,
                                showConfirmButton: false,
                                timer: 1500,
                                timerProgressBar: true,
                                didOpen: (toast) => {
                                    toast.addEventListener('mouseenter', Swal.stopTimer)
                                    toast.addEventListener('mouseleave', Swal.resumeTimer)
                                    //this.$router.push({ path: '/recru/detail/' + this.recruId, });
                                }
                            })
                            this.$router.push({name : 'RecruList'})
                        } else {
                            Swal.fire({
                                icon: 'error',
                                title: 'Ïã§Ìå®ÎêòÏóàÏäµÎãàÎã§',
                                text:'ÏÑúÎ≤ÑÎ•º Îã§Ïãú ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.',
                                toast: true,
                                showConfirmButton: false,
                                timer: 1500,
                                timerProgressBar: true,
                                didOpen: (toast) => {
                                    toast.addEventListener('mouseenter', Swal.stopTimer)
                                    toast.addEventListener('mouseleave', Swal.resumeTimer)
                                }
                            })
                        }
                        console.log(result);
                    })
        },
        sawl : Swal.mixin({
            toast: true,
            showConfirmButton: false,
            timer: 1500,
            timerProgressBar: true,
            didOpen: (toast) => {
                toast.addEventListener('mouseenter', Swal.stopTimer)
                toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
        }),
        sendLinkDefault(){
            try {
                if (Kakao) {
                    Kakao.init('3f57d02e134f1067307cbaec0b42c437');// ÏÇ¨Ïö©ÌïòÎ†§Îäî Ïï±Ïùò JavaScript ÌÇ§ ÏûÖÎ†•
                };
            } catch(e) {};
            var infoTitle = this.recruPost.recruTitle;
            var infoContent = this.recruPost.recruContent;
            var num = this.recruPost.recruId;
